<template>
  <div class="character-list">
    <!-- Navbar -->
    <AppNavbar />

    <!-- Search bar -->
    <div class="search-bar flex-center gap-10 p-20 bg-secondary">
      <input
        v-model="searchQuery"
        type="text"
        placeholder="Search characters..."
        class="search-input w-500 font-medium"
        @input="searchCharacters"
      />
      <button class="btn btn-search" @click="searchCharacters">Search</button>
    </div>

    <!-- Character Flex Container -->
    <div v-if="filteredCharacters.length" class="card-flex-container">
      <div
        v-for="(character, index) in filteredCharacters"
        :key="character.id"
        class="card copy-target"
        :data-clipboard-text="character.htmlCode"
        :data-index="index"
        tabindex="0"
        aria-label="Copy HTML code for {{ character.name }}"
      >
        <!-- Image Wrapper -->
        <div class="image-wrapper">
          <img
            :src="character.path"
            alt="Character Image"
            @error="handleImageError($event)"
            loading="lazy"
          />
          <div class="copy-overlay" v-if="copiedCardId === character.id">
            Copied!
          </div>
        </div>

        <!-- Card Content -->
        <div class="card-content">
          <h3>{{ character.name }}</h3>
          <button
            @click.stop="openCharacterDetails(character)"
            class="btn btn-info"
            aria-label="More information about {{ character.name }}"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="15"
              height="15"
              fill="currentColor"
              class="bi bi-info-circle"
              viewBox="0 0 16 16"
            >
              <path
                d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zm0 13A6 6 0 1 1 8 2a6 6 0 0 1 0 12z"
              />
              <path
                d="M8.93 6.588a.5.5 0 1 0-.854-.354L8 7l-.076-.354a.5.5 0 0 0-.854.354l.252 1.086a.5.5 0 0 0 .487.374h1.224a.5.5 0 0 0 .487-.374l.252-1.086zM8 4.25a.75.75 0 1 1 0-1.5.75.75 0 0 1 0 1.5zM9 11v1H7v-1h2zm0-1V9H7v1h2z"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>

    <!-- No Characters Found Message -->
    <div v-else-if="searchQuery.trim()" class="no-characters">
      <p>No characters found. Try adjusting your search.</p>
    </div>

    <!-- Prompt to Search Message -->
    <div v-else class="all-characters">
      <p>To find an image, please search for a tag.</p>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard';
import { get, ref } from 'firebase/database';
import { db } from '../firebase';
import AppNavbar from './AppNavbar.vue';

export default {
  components: {
    AppNavbar,
  },
  data() {
    return {
      searchQuery: '',
      characters: [], // Loaded from Firebase database
      filteredCharacters: [], // To store search results
      copiedCardId: null, // Track which card was copied
      clipboard: null, // Clipboard instance
    };
  },
  methods: {
    openCharacterDetails(character) {
      this.$router.push({
        name: 'CharacterDetails',
        params: { id: character.id },
      });
    },
    searchCharacters() {
      if (this.searchQuery.trim() === '') {
        this.filteredCharacters = [];
        this.initializeClipboard(); // Reinitialize Clipboard.js after clearing
        return;
      }

      const queryTerms = this.searchQuery
        .toLowerCase()
        .split(',')
        .map((term) => term.trim())
        .filter((term) => term !== '');

      this.filteredCharacters = this.characters.filter((character) => {
        const matchesName = queryTerms.some((term) =>
          character.name.toLowerCase().includes(term)
        );
        const matchesTags =
          character.tags &&
          character.tags.some((tag) =>
            queryTerms.some((query) => tag.toLowerCase().includes(query))
          );
        return matchesName || matchesTags;
      });

      this.$nextTick(() => {
        this.initializeClipboard(); // Initialize Clipboard.js after DOM update
      });
    },
    async fetchCharacters() {
      const characterRef = ref(db, 'characters/');
      try {
        const snapshot = await get(characterRef);
        if (snapshot.exists()) {
          this.characters = Object.entries(snapshot.val()).map(([key, value]) => ({
            id: key,
            ...value,
          }));
          console.log('Fetched Characters:', this.characters); // Debugging Log
          this.searchCharacters();
        } else {
          console.log('No characters found in the database.');
        }
      } catch (error) {
        console.error('Error fetching characters:', error);
      }
    },
    handleCopySuccess(e) {
      const index = parseInt(e.trigger.getAttribute('data-index'));
      const copiedCharacter = this.filteredCharacters[index];
      if (copiedCharacter) {
        this.copiedCardId = copiedCharacter.id;
        setTimeout(() => {
          this.copiedCardId = null;
        }, 2000); // Hide "Copied" after 2 seconds
      }
      e.clearSelection();
    },
    handleCopyError(e) {
      console.error('Clipboard copy failed:', e);
      alert('Failed to copy HTML Code.');
    },
    initializeClipboard() {
      // Destroy existing Clipboard instance if it exists
      if (this.clipboard) {
        this.clipboard.destroy();
      }

      // Initialize Clipboard.js only if there are elements to attach to
      if (this.filteredCharacters.length > 0) {
        this.clipboard = new Clipboard('.copy-target');

        // Success event
        this.clipboard.on('success', this.handleCopySuccess);

        // Error event
        this.clipboard.on('error', this.handleCopyError);
      }
    },
    handleImageError(event) {
      event.target.src = 'https://via.placeholder.com/300x300?text=Image+Not+Found';
    },
  },
  mounted() {
    this.fetchCharacters();
    // Clipboard.js initialization is handled after data fetch via searchCharacters()
  },
  beforeUnmount() {
    // Destroy clipboard instance to prevent memory leaks
    if (this.clipboard) {
      this.clipboard.destroy();
    }
  },
};
</script>

<style scoped>
