.btn-info {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  color: #2980b9; /* Match the original button color */
}

.btn-info svg {
  vertical-align: middle;
}

/* Copy Overlay */
.copy-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 16px;
  z-index: 10;
  opacity: 0;
  pointer-events: none; /* Prevents overlay from blocking clicks */
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px; /* Space between elements if any */
  transition: opacity 0.4s ease, transform 0.4s ease;
}

/* Show the overlay when copiedCardId matches */
.card.copy-target[data-clipboard-text]:focus .copy-overlay,
.card.copy-target:hover .copy-overlay,
.copy-overlay[v-if="copiedCardId === character.id"] {
  opacity: 1;
}

/* Optional: Add subtle shadow to the overlay for better visibility */
.copy-overlay {
